












import goods from '../../template/goods'
import naData from '../../template/no_data'
import loading from 'template/loading'
export default {
	components: {
		naData,
		goods,
		loading
	},
	data() {
		return {
			list: [],
			nodata: false,
			nodataText: '还没有代售商品哦~',
			field: 1,
			zoneOpen: true,
			showPop: false,
			isLoading: false,
		};
	},
	onLoad() {

	},
	onShow() {
		this.getlist()
		this.showLoading()
	},
	methods: {
		showLoading() {
			this.isLoading = true
			console.log(this.isLoading)
			setTimeout(() => {
				this.isLoading = false
				console.log(this.isLoading)
			}, 800);
		},
		getlist() {
			var that = this
			this.$api.get('/api/member/myGoods', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var list = res.data.data
					setTimeout(function(res) {
						that.list = list
						if (that.list.length == 0) {
							that.nodata = true
						}
					}, 800)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
	}
}
